:root{
    --border-image-url: ''
}
.RootContainer{
    height: 100%;
    margin: 0 8px 0 8px;
}
.ImagePaneContainer{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 32px;
    
}
.IconContainer{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 100%;
}
.Icon{
    height: 50px;   
}

.InfoPaneContainer{
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    border-left: 60px solid;
    border-right: 60px solid;
    border-image-source: var(--border-image-url);
    border-image-slice: 0 120;
}
@media(max-width: 900px){
    .RootContainer{
        margin: 0;
    }
    .ImagePaneContainer{
        margin: auto;
        padding: 0;
       
    }
    .InfoPaneContainer{
        border-left: 32px solid;
        border-right: 32px solid;
    }
}
