:root{
    --border-image-url: ''
}
.RootContainer{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin: 16px;
}
.TextContainer{
    width: 100%;
    height: 100%;
    padding: 16px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 16px;
}
.Header{
    display: inline;
    border-bottom: 2px solid #FFECB3;
}